// AboutUs.jsx

import React from 'react';
import './AboutUs.css';
import getImageUrl, { languageTranslate } from "../../util/utils";

const AboutUs = () => {
    const headingAbout = " PROPOS DE NOUS / ABOUT US";
    const tariqImageUrl = getImageUrl('tariq');

    return (
        <div className="about-us-main">
            <div className="about-us-wrapper">
                <h1 className="about-us-heading">{languageTranslate(headingAbout)}</h1>
                <div className="about-us-details">
                    <div className="about-us-intro">
                        <section id="about">
                            <p><strong>Roti Boti</strong>, {languageTranslate("un restaurant gastronomique proposant une cuisine indienne et pakistanaise authentique, donnant vie aux gots vibrants de l'Inde et du Pakistan dans notre communaut. / a fine dining restaurant that features authentic Indian and Pakistani cuisine, bringing the vibrant tastes of India and Pakistan to life in our community.")}</p>

                            <h2>{languageTranslate("Notre Histoire / Our Story")}</h2>
                            <p>{languageTranslate("Fonde en 2013, Roti Boti est situe sur le Boul Cure Labelle, au coeur de Laval, Qubec. Nous nous engageons  servir une cuisine fantastique  la communaut lavalloise, en offrant une exprience culinaire unique et inoubliable. / Established in 2013, Roti Boti is located on Boul Cure Labelle, in the heart of Laval, Quebec. We are dedicated to serving fantastic food to the Laval community, offering a unique and unforgettable dining experience.")}</p>

                            <h2>{languageTranslate("Saveurs Authentiques / Authentic Flavors")}</h2>
                            <p>{languageTranslate("Nos chefs sont originaires du Pakistan et de l'Inde, apportant avec eux les saveurs et les gots authentiques de leur pays d'origine. Grce  leur savoir-faire et leur passion pour la cuisine, ils laborent des plats non seulement dlicieux mais aussi fidles  leurs origines culturelles. Nous sommes fiers d'tre les meilleurs de la ville en matire de cuisine indienne et pakistanaise. / Our chefs hail from Pakistan and India, bringing with them the authentic flavors and tastes of their homeland. With their expertise and passion for cooking, they craft dishes that are not only delicious but also true to their cultural origins. We pride ourselves on being the best in town for Indian and Pakistani cuisine.")}</p>

                            <h2>{languageTranslate("Notre Menu / Our Menu")}</h2>
                            <p>{languageTranslate("Nous proposons une varit de plats de poulet et d'agneau  Halal , ainsi que des plats vgtariens de haute qualit, le tout  des prix avantageux. Notre menu est conu pour rpondre  tous les gots et prfrences, garantissant que chacun y trouvera son bonheur. / We offer a variety of 'Halal' chicken and lamb dishes, as well as high-quality vegetarian options, all at great prices. Our menu is designed to cater to all tastes and preferences, ensuring that there is something for everyone to enjoy.")}</p>

                            <h2>{languageTranslate(" votre porte / At Your Doorstep")}</h2>
                            <p>{languageTranslate("Offrant dsormais la livraison via Skip The Dishes et Uber Eats, vous pouvez dcouvrir les saveurs riches et diverses de l'Inde et du Pakistan dans le confort de votre maison. Que vous dniez sur place ou que vous commandiez au restaurant, Roti Boti s'engage  offrir une exprience culinaire exceptionnelle. / Now offering delivery through Skip The Dishes and Uber Eats, you can experience the rich and diverse flavors of India and Pakistan from the comfort of your home. Whether you are dining in or ordering out, Roti Boti is committed to providing an exceptional culinary experience.")}</p>

                            <h2>{languageTranslate("Commandez maintenant / Order Now")}</h2>
                            <p>{languageTranslate("Venez dcouvrir pourquoi nous sommes le choix privilgi pour la cuisine indienne et pakistanaise  Laval. Visitez-nous aujourd'hui et gotez la diffrence que fait l'authenticit. / Come and discover why we are the preferred choice for Indian and Pakistani cuisine in Laval. Visit us today and taste the difference that authenticity makes.")}</p>

                        </section>
                    </div>
                    <div style={{ marginLeft: "10px" }}>
                        <img className={"tariq-about-us"} src={tariqImageUrl} alt="Chef Tariq preparing a dish" />
                    </div>
                </div>
            </div>
        </div>
    );
}

export default AboutUs;
